.home{
    position: relative;
    height: 600px;
    width: 100%;
    background-image: url('https://img.freepik.com/free-photo/rear-view-programmer-working-all-night-long_1098-18697.jpg?w=1380&t=st=1677072560~exp=1677073160~hmac=f1ab7df437c4bc6dc57e8faba8f42dc40a5dab60386259f122e8fccf40f2012c');
    background-position: center;
    background-size: cover;
}

.overlay {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0px;
    left: 0px;
    background: radial-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0));  
}